import React, { Fragment, useEffect, useState } from "react";
import { Table, Input, Modal, Button, Form, Select } from "antd";
import { useSelector, useDispatch } from "react-redux";
import { QuanLyNguoiDungReducer } from "./../../../redux/reducers/QuanLyNguoiDungReducer";
import {
  capNhatThongTinNguoiDungAction,
  layDanhSachNguoiDungAction,
  layThongTinNguoiDungAction,
  timKiemNguoiDungAction,
  xoaNguoiDungAction,
  themNguoiDungAction,
} from "./../../../redux/actions/QuanLyNguoiDungAction";
import { NavLink } from "react-router-dom";
import {
  EditOutlined,
  DeleteOutlined,
  ContactsOutlined,
  UserOutlined,
  EyeInvisibleOutlined,
  EyeTwoTone,
} from "@ant-design/icons";
import "./User.scss";
import { GROUPID } from "./../../../util/settings/config";

const { Option } = Select;

export default function User() {
  const { listUser, listUserTimKiem, keyword } = useSelector(
    (state) => state.QuanLyNguoiDungReducer
  );
  const dispatch = useDispatch();

  //To modal update hin th thng tin User
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [modalInfo, setModalInfo] = useState({
    title: "",
    okText: "",
    cancelText: "",
  });
  const showModal = () => {
    form.resetFields();
    setIsModalVisible(true);
  };

  const handleOk = () => {
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };

  useEffect(() => {
    dispatch(layDanhSachNguoiDungAction());
  }, []);

  //  to user c chn khi edit
  let [selectUser, setSelectUser] = useState({});
  const setUser = (user) => {
    user = { ...user, isUpdate: true };
    return setSelectUser(user);
  };

  // cp nht user c chn  render ra modal
  useEffect(() => {
    console.log(selectUser);
    form.setFieldsValue(selectUser);
  }, [selectUser]);

  const columns = [
    {
      title: "STT",
      dataIndex: "stt",
      width: 50,
    },
    {
      title: "Tn ti khon",
      dataIndex: "userName",
      width: 100,
    },
    {
      title: "H v tn",
      dataIndex: "name",
      width: 150,
    },
    {
      title: "Email",
      dataIndex: "email",
      width: 200,
    },
    {
      title: "S in thoi",
      dataIndex: "phoneNumber",
      width: 100,
    },
    {
      title: "Mt khu",
      dataIndex: "password",
      width: 100,
    },
    {
      title: "Loi ngi dng",
      dataIndex: "loaiND",
      width: 100,
    },
    {
      title: "",
      dataIndex: "action",
      width: 150,
    },
  ];

  const data = [];

  const [form] = Form.useForm();

  if (keyword === "") {
    for (let i = 0; i < listUser.length; i++) {
      data.push({
        key: i,
        stt: i + 1,
        userName: listUser[i].taiKhoan,
        name: listUser[i].hoTen,
        email: listUser[i].email,
        phoneNumber: listUser[i].soDt,
        password: listUser[i].matKhau,
        loaiND: listUser[i].maLoaiNguoiDung,
        action: (
          <Fragment>
            {/* <NavLink to="" className="text-green-700 text-2xl mr-5">
              <ContactsOutlined />
            </NavLink> */}
            <EditOutlined
              onClick={() => {
                setUser(listUser[i]);
                setModalInfo({
                  title: "Cp nht thng tin ngi dng",
                  okText: "Cp nht",
                  cancelText: "Hy",
                });
                showModal();
              }}
              className="text-blue-700 text-2xl mr-5"
            />

            <span
              className="text-red-700 text-2xl cursor-pointer mr-5"
              to="/"
              onClick={() => {
                if (
                  window.confirm(
                    "Bn c mun xa ngi dng " + listUser[i].taiKhoan
                  )
                ) {
                  // Gi action
                  dispatch(xoaNguoiDungAction(listUser[i].taiKhoan));
                }
              }}
            >
              <DeleteOutlined />
            </span>
          </Fragment>
        ),
      });
    }
  } else {
    for (let i = 0; i < listUserTimKiem.length; i++) {
      data.push({
        key: i,
        stt: i + 1,
        userName: listUserTimKiem[i].taiKhoan,
        name: listUserTimKiem[i].hoTen,
        email: listUserTimKiem[i].email,
        phoneNumber: listUserTimKiem[i].soDt,
        password: listUserTimKiem[i].matKhau,
        loaiND: listUserTimKiem[i].maLoaiNguoiDung,
        action: (
          <Fragment>
            <NavLink to="" className="text-green-700 text-2xl mr-5">
              <ContactsOutlined />
            </NavLink>
            <EditOutlined
              onClick={() => {
                setUser(listUserTimKiem[i]);
                setModalInfo({
                  title: "Cp nht thng tin ngi dng",
                  okText: "Cp nht",
                  cancelText: "Hy",
                });
                showModal();
              }}
              className="text-blue-700 text-2xl mr-5"
            />

            <span
              className="text-red-700 text-2xl cursor-pointer mr-5"
              to="/"
              onClick={() => {
                if (
                  window.confirm(
                    "Bn c mun xa ngi dng " + listUserTimKiem[i].taiKhoan
                  )
                ) {
                  // Gi action
                  dispatch(xoaNguoiDungAction(listUserTimKiem[i].taiKhoan));
                }
              }}
            >
              <DeleteOutlined />
            </span>
          </Fragment>
        ),
      });
    }
  }

  const { Search } = Input;
  const onSearch = (value) => {
    dispatch(timKiemNguoiDungAction(value));
  };
  function handleChange(value) {
    console.log(value); // { value: "lucy", key: "lucy", label: "Lucy (101)" }
  }
  return (
    <Fragment>
      <Button
        type="primary"
        className="mb-5"
        onClick={() => {
          setModalInfo({
            title: "Thm ngi dng mi",
            okText: "Thm",
            cancelText: "Hy",
          });
          delete selectUser.isUpdate;
          showModal();
        }}
      >
        Thm ngi dng
      </Button>
      <Search
        placeholder="Nhp tn ti khon cn tm"
        // onSearch={onSearch}
        onChange={(e) => onSearch(e.target.value)}
        enterButton
        className="mb-5"
      />
      <Table
        columns={columns}
        dataSource={data}
        pagination={{ pageSize: 10 }}
        scroll={{ y: "50vh" }}
      />
      <Modal
        title={modalInfo.title}
        visible={isModalVisible}
        forceRender
        okText={modalInfo.okText}
        cancelText={modalInfo.cancelText}
        onOk={() => {
          //check validation
          form
            .validateFields()
            .then((values) => {
              //Update tn nhm vo thng tin user
              const updateUser = { ...selectUser, maNhom: GROUPID };
              if (updateUser.isUpdate) {
                delete updateUser.isUpdate;
                // Gi API update
                dispatch(capNhatThongTinNguoiDungAction(updateUser, true));
              } else {
                //Gi API add user
                dispatch(themNguoiDungAction(updateUser));
              }

              // tt Modal
              handleOk();
            })
            .catch((info) => {
              console.log("Validate Failed:", info);
            });
        }}
        onCancel={handleCancel}
      >
        <Form
          form={form}
          labelCol={{ span: 6 }}
          wrapperCol={{ span: 14 }}
          layout="horizontal"
          initialValues={{ maLoaiNguoiDung: "KhachHang" }}
          onFieldsChange={(a, b) => {
            selectUser = { ...selectUser, [a[0].name]: a[0].value };
          }}
        >
          <Form.Item
            name="taiKhoan"
            label="Tn ti khon"
            rules={[
              {
                required: true,
                message: "Vui lng nhp vo tn ti khon!",
              },
              {
                min: 5,
                message: "Tn ti khon ti thiu 5 k t tr ln.",
              },
            ]}
          >
            <Input disabled={selectUser.isUpdate} />
          </Form.Item>
          <Form.Item
            name="hoTen"
            label="H v tn"
            rules={[
              {
                required: true,
                message: "Vui lng nhp vo h v tn!",
                whitespace: true,
              },
            ]}
          >
            <Input />
          </Form.Item>
          <Form.Item
            label="Email"
            name="email"
            rules={[
              {
                type: "email",
                message: "E-mail khng hp l!",
              },
              {
                required: true,
                message: "Vui lng nhp vo E-mail!",
              },
            ]}
          >
            <Input />
          </Form.Item>
          <Form.Item
            label="S in thoi"
            name="soDt"
            rules={[
              {
                required: true,
                message: "Nhp vo s in thoi!",
              },
              {
                pattern: "^([+84|84|0]+(3|5|7|8|9|1[2|6|8|9]))+([0-9]{8})$",
                message: "S in thoi khng ng nh dng",
              },
            ]}
          >
            <Input />
          </Form.Item>
          <Form.Item
            label="Mt khu"
            name="matKhau"
            rules={[
              {
                required: true,
                message: "Vui lng nhp vo mt khu!",
              },

              { min: 8, message: "Mt khu ti thiu 8 k t tr ln." },
              {
                pattern: ".*[A-Z].*$",
                message: "Mt khu cha t nht 1 ch ci in hoa",
              },
              {
                pattern: ".*[0-9].*$",
                message: "Mt khu cha t nht 1 ch s",
              },
              {
                pattern: ".*[!@#$%^&+=].*$",
                message: "Mt khu cha t nht 1 k t c bit",
              },
              {
                pattern: "^(?:[\u0000-\u007F]+|[\u0370-\u03FF]+)$",
                message: "Mt khu khng ng nh dng",
              },
            ]}
          >
            <Input.Password
              iconRender={(visible) =>
                visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />
              }
            />
          </Form.Item>

          <Form.Item name="maLoaiNguoiDung" label="Loi ti khon">
            <Select className="text-black">
              <Option value="KhachHang">Khch hng</Option>
              <Option value="QuanTri">Qun Tr</Option>
            </Select>
          </Form.Item>
        </Form>
      </Modal>
    </Fragment>
  );
}
